.react-contextmenu {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: .25rem;
    color: #373a3c;
    font-size: 16px;
    margin: 2px 0 0;
    min-width: 160px;
    outline: none;
    opacity: 0;
    padding: 5px 0;
    pointer-events: none;
    text-align: left;
    transition: opacity 250ms ease !important;
}

.react-contextmenu.react-contextmenu--visible {
    opacity: 1;
    pointer-events: auto;
    z-index: 9999;
}

.react-contextmenu-item {
    background: 0 0;
    border: 0;
    color: #373a3c;
	cursor: pointer;
    font-weight: 400;
    line-height: 1.5;
    padding: 3px 20px;
    text-align: inherit;
    white-space: nowrap;
}

.react-contextmenu-item.react-contextmenu-item--active,
.react-contextmenu-item.react-contextmenu-item--selected {
    color: #fff;
    background-color: #20a0ff;
    border-color: #20a0ff;
    text-decoration: none;
}

.react-contextmenu-item.react-contextmenu-item--disabled,
.react-contextmenu-item.react-contextmenu-item--disabled:hover {
    background-color: transparent;
    border-color: rgba(0,0,0,.15);
    color: #878a8c;
}

.react-contextmenu-item--divider {
    border-bottom: 1px solid rgba(0,0,0,.15);
    cursor: inherit;
    margin-bottom: 3px;
    padding: 2px 0;
}
.react-contextmenu-item--divider:hover {
    background-color: transparent;
    border-color: rgba(0,0,0,.15);
}

.react-contextmenu-item.react-contextmenu-submenu {
	padding: 0;
}

.react-contextmenu-item.react-contextmenu-submenu > .react-contextmenu-item {
    padding: 0;
}

.react-contextmenu-item.react-contextmenu-submenu > .react-contextmenu-item:after {
    content: "▶";
    display: inline-block;
    position: absolute;
    right: 7px;
}

.example-multiple-targets::after {
    content: attr(data-count);
    display: block;
}

.react-contextmenu-wrapper {
    width: 100%;
    height: 100%;
}
/* FONT IMPORTS */

.calendar-icon {
  font-family: 'Material Icons', serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'liga';
}


/* VARIABLES */

:root {
  --main-color: #ff8000;
  --text-color: #777;
  --text-color-light: #ccc;
  --border-color: #eee;
  --bg-color: #f9f9f9;
  --neutral-color: #fff;
}


/* GENERAL */

* {
  box-sizing: border-box;
}

.calendar-header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
  background: #fff;
  background: var(--neutral-color);
}

.calendar-header #logo {
  font-size: 175%;
  text-align: center;
  color: #ff8000;
  color: var(--main-color);
  line-height: 1;
}

.calendar-header #logo .calendar-icon {
  padding-right: .25em;
}


/* GRID */

.calendar-grid-row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.calendar-grid-row-middle {
  align-items: center;
}

.calendar-grid-col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.calendar-grid-col-start {
  justify-content: flex-start;
  text-align: left;
}

.calendar-grid-col-center {
  justify-content: center;
  text-align: center;
}

.calendar-grid-col-end {
  justify-content: flex-end;
  text-align: right;
}


/* Calendar */

.calendar {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  background: #fff;
  background: var(--neutral-color);
  border: 1px solid #eee;
  border: 1px solid var(--border-color);
}

.calendar .calendar-header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
}

.calendar .calendar-header .calendar-button {
  cursor: pointer;
  transition: .15s ease-out;
  color: black;
  outline: none;
  border: none;
  background-color: white;
}

.calendar .calendar-header .calendar-button:hover {
  transform: scale(1.75);
  transition: .25s ease-out;
}

.calendar .calendar-header .calendar-button:first-of-type {
  margin-left: 1.5rem;
}

.calendar .calendar-header .calendar-button:last-of-type {
  margin-right: 1.5rem;
}

.calendar .calendar-days {
  text-transform: uppercase;
  font-weight: 600;
  color: #ccc;
  color: var(--text-color-light);
  font-size: 80%;
  padding: .75em 0;
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
}

.calendar .calendar-body .calendar-cell {
  position: relative;
  height: calc(120vh/8);
  border-right: 1px solid #eee;
  border-right: 1px solid var(--border-color);
  overflow: hidden;
  background: #fff;
  background: var(--neutral-color);
  transition: 0.25s ease-out;
}

.calendar .calendar-body .calendar-cell div {
    align-self: center;
    text-align: center;
    justify-self: center;
}

.calendar .calendar-body .selected {
  border-left: 10px solid transparent;
  border-image: linear-gradient(45deg, #ff8000 0%, #ff8000 40%);
  border-image: linear-gradient(45deg, var(--main-color) 0%, var(--main-color) 40%);
  border-image-slice: 1;
}

.calendar .calendar-body .calendar-grid-row {
  border-bottom: 1px solid #eee;
  border-bottom: 1px solid var(--border-color);
}

.calendar .calendar-body .calendar-grid-row:last-child {
  border-bottom: none;
}

.calendar .calendar-body .calendar-cell:last-child {
  border-right: none;
}

.calendar .calendar-body .calendar-cell .calendar-number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: .75em;
  right: .75em;
  font-weight: 700;
}

.calendar .calendar-body .disabled {
  color: #ccc;
  color: var(--text-color-light);
  pointer-events: none;
}

.calendar .calendar-body .calendar-cell .calendar-bg {
  font-weight: 700;
  line-height: 1;
  color: #ff8000;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -.2em;
  right: -.05em;
  transition: .25s ease-out;
  letter-spacing: -.07em;
}

.calendar .calendar-body .calendar-grid-col {
  flex-grow: 0;
  flex-basis: calc(100%/7);
  width: calc(100%/7);
}

/* Events */
  
.calendar-event-list {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  list-style: none;
}

.calendar-event-list li {
  text-align: center;
  margin: 1px 0;
  width: 90%;
}

.calendar-event, .calendar-event-dis {
  text-align: center;
  color: white;
  border: none;
  background: #ff8000;
  background: var(--main-color);
  cursor: pointer;
  transition: .15s ease-out;
}

.calendar-event:hover {
  transform: scale(1.1);
  transition: .25s ease-out;
  z-index: 20;
  margin: 5px 5px;
}
