    margin-bottom: 10px;
}

.session-form-info h1 {
    font-size: 72px;
    font-weight: 500;
}

.session-form-info h3 {
    font-size: 35px;
    font-weight: 500;
}

.session-info-title {
    font-size: 32px;
}

.session-info-color {
    width: 250px;
}

.session-form-info .session-form-desc {
    margin-top: 15px;
}

.session-form-info .session-form-total {
    text-align: left;
}

.session-form-info .button {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    max-width: 500px;
}

/* Modal */

.modal-background {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.7);
}

.modal-background .modal-content, .modal-content-sm {
    position: relative;
    background-color: #fefefe;
    border: 1px solid #888888;
    width: 100%;
    height: 100%;
}
.modal-content-sm {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 40px;
}

.modal-button {
    position: absolute;
    width: 45px;
    height: 35px;
    padding: 0;
    top: 10px;
    right: 10px;
    z-index: 100;
}

.animate {
    animation: animatezoom 0.8s;
}

@keyframes animatezoom {
    from {transform: scale(0);}
    to {transform: scale(1);}
}

/* Table */

.table {
    border-collapse: collapse;
    width: 100%;
    table-layout: auto;
}

.table td, .table th {
    border: 1px solid rgba(0, 0, 0, 0.137);
    padding: 8px;
}

.table tr:hover {
    background-color: #d8d8d860
}

.table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #ff8000;
    background-color: var(--orange);
    color: white;
}

.table-title-group {
    display: inline-flex;
    position: relative;
    width: 100%;
    margin-bottom: 10px;
}

.table-title-group .button {
    position: absolute;
    right: 0;
}

/* Signature */

.signature {
    border: 1px solid black;
}
